.header {
  width: 100%;
  padding: 45px 0 37px;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
  margin: 0 auto 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media (max-width: 930px) {
  .header {
    margin-bottom: 42px;
    padding: 28px 0 32px;
  }
}
